import { Component, OnInit, OnDestroy } from '@angular/core';
import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingService } from './services/loading/loading.service'
import { Location } from '@angular/common';
import { ConfigurationsService } from './services/configurations/configurations.service'

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent implements OnInit, OnDestroy {

  predictions = [
    {
      title: 'Predicciones',
      url: '/tabs/predictions',
      icon: 'football'
    },
    {
      title: 'Rankings',
      url: '/tabs/rankings',
      icon: 'ribbon'
    },
    {
      title: 'Noticias',
      url: '/tabs/news',
      icon: 'paper'
    },
    {
      title: 'Mis Puntos',
      url: '/tabs/my-points',
      icon: 'stats'
    }
  ];

   statistics = [
   {
      title: 'Partidos',
      url: '/tabs-statistics/matches',
      icon: 'football'
    },
    {
      title: 'En vivo',
      url: '/tabs-statistics/live',
      icon: 'alarm'
    }
  ];

  session = true;

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private router: Router,
    private loading: LoadingService,
    private ionRouter : Location,
    private config : ConfigurationsService,
  ) {
    this.initializeApp();
  }

  ngOnInit() {

    this.router.events.subscribe((event: RouterEvent) => {
      if (event instanceof NavigationEnd) {
        this.predictions.map( p => {
          return p['active'] = (event.url === p.url);
        });
      }
    });
    this.inStatistics ();
  }

  inStatistics() {
     this.router.events.subscribe((event: RouterEvent) => {
      if (event instanceof NavigationEnd) {
       this.statistics.map( p => {
          return p['active'] = (event.url === p.url);
        });
      }
    });
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();

    // active hardware back button
      document.addEventListener("backbutton", () => { 
        if ( this.router.url === '/tabs-statistics/live' || this.router.url === '/tabs-statistics/matches'  || this.router.url === '/tabs/news' || this.router.url === '/tabs/rankings' || this.router.url === '/tabs/my-points') {
          this.loading.presentAlertConfirm(
            'Alerta',
            '',
            '¿Estás seguro que quieres salir de la aplicación?',
            [
              {
                text: 'Cancelar',
                handler: () => {         
                  console.log('Cancel')
                }
              },
              {
                text: 'Aceptar',
                handler: () => {
                  navigator['app'].exitApp();
                }
              }
            ]
          ) 
        }else {
          this.goBack()
        } 
      });
    });

    // pause and resume sesion
    this.platform.resume.subscribe(async () => {
      if(this.session === false){
        this.router.navigateByUrl('/tabs/predictions');
        this.session = true;
      }

    });

    this.platform.pause.subscribe(async () => {
      this.session = false;
      console.log("pasó a segundo plano ")
    });
  }

  logOut() {
    this.router.navigate(['/login']);  
    this.ngOnDestroy();
  }

  ngOnDestroy() {
    localStorage.removeItem('id_client');
  }

  goBack(){
    this.ionRouter.back()
  }

}
